import React, { useState } from 'react'

const api = {
  key: 'ccdd85907e2b3734470670283050f9f6',
  base: 'https://api.openweathermap.org/data/2.5/'
}

function App() {
  const [query, setQuery] = useState('');
  const [weather, setWeather] = useState({});

  const search = (evt) => {
    if (evt.key === "Enter") {
      fetch(`${api.base}weather?q=${query}&units=metric&APPID=${api.key}`)
        .then(res => res.json())
        .then(result => {
          setWeather(result);
          setQuery('');
        });
    }
  }

  const dateBuilder = (d) => {
    let months = ['January', 'February', 'March', 'April', 'May', 
      'June', 'July', 'August', 'September', 'October', 'November', 
      'December'];
    let days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 
      'Friday', 'Saturday'];

      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();
      return `${day} ${date} ${month} ${year}` 
  }

  return (
    <div className={
      (typeof weather.main != 'undefined') ? 
      ((weather.main.temp > 16) ? 
      'app warm' :
      'app') :
      'app'
    }>
      <main>
        <div className="container">
          <h1 className="main-heading">An Instant Weather Application</h1>
          <div className="search-box">
            <input 
              type="text" 
              className="search-bar"
              placeholder="Enter location..."
              onChange={e => setQuery(e.target.value)}
              value={query}
              onKeyPress={search}
            />
          </div>

          {
            (typeof weather.main != 'undefined')
            ?
            (
              <>
              <div className="location-box">
                <div className="location">{weather.name}, {weather.sys.country}</div>
                <div className="date">{dateBuilder(new Date())}</div>
              </div>
              <div className="weather-box">
                <div className="current-temp">
                  {Math.round(weather.main.temp)}°c
                </div>
                <div className="weather">
                  <p><span>Weather:</span> {weather.weather[0].description}</p>
                  <p><span>Feels like:</span> {weather.main.feels_like}°c</p>
                  <p><span>Humidity:</span> {weather.main.humidity}%</p>
                  <p><span>Air pressure:</span> {weather.main.pressure} mmHg</p>
                </div>
              </div>
              </>
            ) : (
              <>
                <div className="weather">
                  <p>Please enter location</p>
                </div>
              </>
            )
          }
          
        </div>
      </main>
    </div>
  );
}

export default App;
